import { React, useState, useEffect } from 'react';
import CartItem from '../CartItem/CartItem';
import CartEmpty from '../CartEmpty/CartEmpty';
import './CartMenu.scss';


function CartMenu({items, deleteCartItem, hideCartMenu, cartValueSum}) {
  const [cartTax, setCartTax] = useState(0);
  useEffect(() => {
    setCartTax(parseInt(cartValueSum * 0.05))
  },[cartValueSum]);
  return (
    <div className='overlay'>
      <div className="cartMenu">
        <button className="cartMenu-button" onClick={() => hideCartMenu()}>
          <svg width="20" height="20" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M9.0799 7.61553L6.6311 5.16673L9.07982 2.71801C10.0241 1.77376 8.55964 0.309342 7.6154 1.25359L5.16668 3.70231L2.71787 1.2535C1.77384 0.309466 0.309467 1.77384 1.2535 2.71787L3.70231 5.16668L1.25359 7.61539C0.309343 8.55964 1.77376 10.0241 2.71801 9.07982L5.16673 6.6311L7.61553 9.0799C8.55969 10.0241 10.0241 8.55969 9.0799 7.61553Z" fill="#D3D3D3"/>
          </svg>    
        </button>
        <div className="cartMenu--topper">
          <h3 className="cartMenu-title"></h3>
          <div className="cartMenu__items">
            {items.map((item) => (
              <CartItem 
                key={item.name}
                id={item.id}
                title={item.title} 
                price={item.price} 
                imgUrl={item.imgUrl}
                deleteCartItem={deleteCartItem}
              ></CartItem>
            ))}
          </div>
        </div>
        <div className="cartMenu__order">
          <div className="cartMenu__sum">
              <h3 className="cartMenu__order-title">: </h3>
              <p className='cartMenu__order-value'>{cartValueSum} . </p>
          </div>
          <div className="cartMenu__tax">
              <h3 className="cartMenu__order-title"> 5%: </h3>
              <p className='cartMenu__order-value'>{cartTax} .</p>
          </div>
          <button className='cartMenu__order-button'> </button>
        </div>
      </div>
    </div>
  );
}

export default CartMenu;
